export const playAudio = (mp3) => {
    let audio = new Audio();
    audio.src = mp3;
    audio.isCanPlay = false;
    audio.oncanplaythrough = () => {
        audio.isCanPlay = true;
    };
    audio.timeId = null;
    return {
        play: () => {
            if (audio) {
                if (audio.isCanPlay) {
                    audio.currentTime = 0;
                    audio.play();
                } else if (!audio.timeId) {
                    audio.timeId = setTimeout(() => {
                        audio.timeId = null;
                        audio.play();
                    }, 100);
                }
            }
        },
        pause: () => {
            if (audio) {
                if (audio.timeId) {
                    clearTimeout(audio.timeId);
                }
                audio.pause();
            }
        },
        destroy: () => {
            if (audio) {
                if (audio.timeId) {
                    clearTimeout(audio.timeId);
                }
                audio.pause();
                audio.pause();
                audio.src = null;
                audio.oncanplaythrough = null;
                audio = null;
            }
        }
    };
};
