<template>
    <div class="wrap">
        <div class="title">请选择您要办理的业务</div>
        <div class="page-tip" v-if="totalPages > 1 && menuState === 2">
            共{{ totalPages }}页，当前第{{ currentPage }}/{{ totalPages }}页
        </div>
        <div :class="menuState === 1 ? 'menuWrap' : 'subMenuWrap'">
            <template v-for="(item,index) in menuList">
                <div class="itemWrap" v-if="item" :key="index">
                    <div v-if="menuState === 1 && Math.ceil((index+1)/pageNum) === currentPage" @click="enterSubMenu(item)">
                        <span class="kedaIconfont" :class="item.icon" :key="index"></span>
                        <span>{{ item.name }}</span>
                    </div>
                    <template v-for="(subItem,subIndex) in item.children">
                        <div
                            :key="subIndex"
                            v-if="subItem && menuState === 2 && menuId === item.id && Math.ceil((subIndex+1)/subPageNum) === currentPage"
                            @click="goto(subItem)"
                        >
                            <div>
                                <span class="kedaIconfont" :class="subItem.icon"></span>
                                <span>{{ subItem.name }}</span>
                                <span>{{ !noWarnText && subItem.disabledBusiness ? '（正在维护中，敬请期待！）' : '' }}</span>
                            </div>
                        </div>
                    </template>
                </div>
            </template>
        </div>
        <div v-if="currentPage > 1 && totalPages > 1" class="pageUp kedaIconfont kedaIcon-pageUp" @click="upPage"></div>
        <div v-if="currentPage < totalPages && totalPages > 1" class="pageDown kedaIconfont kedaIcon-pageUp" @click="downPage"></div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="首页" type-class="home"/>
            <kd-button icon="kedaIcon-zhongzhi" text="返回" :button-handle="backMenu"/>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex';
import { changeRouter } from '@/utils';
import audioObj from '@/assets/media/common/1.mp3';
import { playAudio } from '../../../utils/media';

export default {
    name: 'MenuMenu',
    data () {
        return {
            noWarnText: Boolean(window.localStorage.getItem('noWarnText')),
            pageNum: 3,
            subPageNum: 6,
            audioObj: null
        };
    },
    mounted () {
        this.audioObj = playAudio(audioObj);
        this.audioObj.play();
        if (this.menuList && this.menuList.length === 1) {
            this.changeMenuPageState({
                menuState: 2,
                menuId: this.menuList[0].id
            });
        }
    },
    beforeDestroy () {
        this.audioObj.destroy();
    },
    computed: {
        totalPages () {
            if (this.menuState === 1) {
                return Math.ceil(this.menuList.length / this.pageNum);
            } else {
                const firstArr = this.menuList.find((i) => {
                    return i && i.id === this.menuId;
                });
                return Math.ceil(firstArr.children.length / this.subPageNum);
            }
        },
        ...mapState('common', {
            menuList: 'menuList',
            userBusinessId: 'userBusinessId',
            businessTypeCode: 'businessTypeCode',
            currentPage: 'currentPage', // 当前页数
            menuState: 'menuState', // 1:显示一级导航2：显示二级导航
            menuId: 'menuId' // 菜单一级Id
        }),
        ...mapState('permanent', {
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        })
    },
    methods: {
        ...mapMutations('common', ['setCurrentBusinessHandle', 'setCurrentBusinessName', 'changeTipPage', 'changeMenuPageState', 'changeVisible']),
        ...mapActions('common', ['businessStart']),
        hasData () {
            if (this.menuState === 1 && this.menuList.length > 0) {
                return false;
            } else if (this.menuState === 2) {
                const list = this.menuList.find((item) => {
                    return this.menuId === item.id;
                });
                if (list && list.children && list.children.length > 0) {
                    return false;
                }
            }
            return true;
        },
        goto (item) {
            if (item.disabledBusiness) {
                return;
            }
            if (item.code) {
                this.setCurrentBusinessHandle(item.code);
                this.setCurrentBusinessName(item.name);

                // todo 测试用
                if (this.CONSTANT.disableHandRequest) {
                    this.changeVisible({ visibleSteps: true, visibleTip: false, visibleBusinessName: true, visibleJurisdiction: true });
                    changeRouter('/' + item.code);
                } else {
                    // todo 社保和业务指南直接进
                    if (item.code === 701 || item.code === 801 || item.code === 508) {
                        changeRouter('/' + item.code);
                    } else {
                        this.businessStart({ path: '/' + item.code });
                    }
                }
            }
        },
        enterSubMenu (item) {
            if (item.disabledBusiness) {
                return;
            }
            if (item.code) {
                if (item.disable) {
                    return;
                }
                this.setCurrentBusinessHandle(item.code);
                this.setCurrentBusinessName(item.name);

                // todo 测试用
                if (this.CONSTANT.disableHandRequest) {
                    this.changeVisible({ visibleSteps: true, visibleTip: false, visibleBusinessName: true, visibleJurisdiction: true });
                    changeRouter('/' + item.code);
                } else {
                    this.businessStart({ path: '/' + item.code });
                }
            } else {
                this.audioObj.play();
                this.changeMenuPageState({ menuState: 2, menuId: item.id, currentPage: 1 });
            }
        },
        backMenu () {
            if (this.menuState === 1) {
                changeRouter('/');
            } else {
                if (this.menuList && this.menuList.length === 1) {
                    changeRouter('/');
                    return;
                }
                this.changeMenuPageState({
                    menuState: 1,
                    currentPage: 1
                });
            }
        },
        upPage () {
            if (this.currentPage > 1) {
                this.changeMenuPageState({
                    currentPage: this.currentPage - 1
                });
            }
        },
        downPage () {
            if (this.currentPage < this.totalPages) {
                this.changeMenuPageState({
                    currentPage: this.currentPage + 1
                });
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .title {
        box-sizing: border-box;
        font-size: 40px;
        font-weight: 500;
        text-align: center;
        color: #ffffff;
        padding-top: 40px;
        padding-bottom: 20px;
    }

    .wrap {
        /*display: flex;*/
        align-items: center;
        flex-direction: column;
        /*justify-content: space-evenly;*/
        .page-tip {
            font-size: 26px;
            color: #fff;
            text-align: center;
        }

        .pageUp {
            position: absolute;
            left: 30px;
            top: 50%;
            margin-top: -20px;
            font-size: 60px;
            color: #FFFFFF;
            transform: rotate(-90deg);
        }

        .pageDown {
            position: absolute;
            right: 30px;
            top: 50%;
            margin-top: -20px;
            font-size: 60px;
            color: #FFFFFF;
            transform: rotate(90deg);
        }

        .menuWrap {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .itemWrap {
                width: 60vw;
                cursor: pointer;
                display: flex;
                flex-direction: column;

                > div {
                    margin: 10px 0;
                    width: 100%;
                    line-height: 110px;
                    background: linear-gradient(#70A3D8, #3A84BF);
                    border: 1PX solid #EDFFFF;/*no*/
                    border-radius: 10px;
                    box-sizing: border-box;
                    padding: 0 35px;

                    span {
                        color: #ffffff;
                        font-size: 30px;
                    }

                    span:first-child {
                        border-right: 2px solid #8FC9FF;
                        padding-right: 20px;
                        display: inline-block;
                        height: 50px;
                        line-height: 50px;
                        width: 40px;
                    }

                    span:last-child {
                        padding-left: 20px;
                    }
                }
            }
        }

        .subMenuWrap {
            width: 100%;
            margin-top: 20px;
            .itemWrap {
                width: 80%;
                margin: 0 auto;
                display: flex;
                flex-flow: wrap;

                > div {
                    width: 33.3%;
                    height: 240px;
                    box-sizing: border-box;
                    padding: 0 15px;
                    margin-bottom: 30px;

                    > div {
                        display: flex;
                        flex-flow: column;
                        background: linear-gradient(#70A3D8, #3A84BF);
                        border: 1PX solid #EDFFFF;/*no*/
                        border-radius: 10px;
                        box-sizing: border-box;
                        justify-content: center;
                        height: 100%;
                    }

                    span {
                        color: #ffffff;
                        font-size: 20px;
                        text-align: center;
                    }

                    span:first-child {
                        font-size: 70px;
                        margin-bottom: 10px;
                    }

                    span:last-child {
                        box-sizing: border-box;
                        padding: 5px 10px 0;
                        font-size: 14px;
                    }
                }
            }
        }

    }

</style>
